.pchide{
  display: none;
}
@media (max-width:767px){
  .pchide{
    display: unset;
  }
  .sidenav {
    padding-top: 150px;
    height: 100%;
  width: 0;
  /* position: fixed;
  z-index: 1; */
  top: 0;
  left: 0;
  background-color: #111;
  overflow-x: hidden;
  transition: 0.5s;
  }
  .sidenav a {
    padding: 8px 8px 8px 32px;
  text-decoration: none;
  color: #818181;
  display: block;
  transition: 0.3s;
    font-size: 18px;
  }
  .sidenav a:hover {
    color: #f1f1f1;
  }
  
  .sidenav .closebtn {
    position: absolute;
    top: 30;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
  }
  .mhide{
    display: none;
  }
}